






import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({})
export default class SvgIcon extends Vue {
    @Prop({
        type: String,
        required: true,
        default: ''
    })
    name!: string;

    @Prop({
        type: String,
        default: ''
    })
    className!: string;

    get iconName() {
        return `#icon-${this.name}`;
    }

    get svgClass() {
        if (this.className) {
            return "svg-icon " + this.className;
        } else {
            return "svg-icon";
        }
    }
    
    // mounted() {
    //     // console.log('mounted', this.name, this.className);
    // }
}
